.homePage {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}

.home {
  grid-column: 1/-1;
  position: relative;
}

.home__search {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  top: 5px;
  left: 5px;
  z-index: 450
  
}

.home__search .home__input {
    border-radius: 3px;
    border: 2px solid rgba(0,0,0,0.2);
    height: 30px;
    padding: 0 30px 0 10px;
    font-family: 'Montserrat';
  }

.home__search .home__search {
    position: relative;
    cursor: pointer;
    height: 30px;
    width: 30px;
    padding: 0;
    margin-left: -35px;
    background: none;
    border: none
  }

.home__search .home__search:before {
      content: '';
      display: block;
      height: 30px;
      width: 30px;
      position: absolute;
      top: -4px;
      background: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='30px' height='30px' viewBox='0 0 512 512' stroke='%234c4d49'%3E %3Ctitle%3Eionicons-v5-f%3C/title%3E %3Cpath d='M221.09,64A157.09,157.09,0,1,0,378.18,221.09,157.1,157.1,0,0,0,221.09,64Z' style='fill:none;stroke-miterlimit:10;stroke-width:32px'/%3E %3Cline x1='338.29' y1='338.29' x2='448' y2='448' style='fill:none;stroke-linecap:round;stroke-miterlimit:10;stroke-width:32px'/%3E %3C/svg%3E")
    }