.portal {
  position: absolute;
  bottom: 5%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  min-width: 160px;
  border-radius: 3px;
  z-index: 500
}

.portal.portal--sucess {
    background-color: #63d471;
  }

.portal.portal--fail {
    background-color: #d7263d;
  }

.portal__overlay {
  background: none;
}

.portal__message {
  margin: 0;
  padding: 10px 5px;
  text-align: center;
  color: white;
}