.header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  position: relative;
  height: 60px;
  background: #4c4d49;
}

.header__logo {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center
}

.header__logo > img {
    -webkit-transform: scale(0.75);
            transform: scale(0.75);
  }

.header__logo > span {
    visibility: hidden;
    display: block;
    width: 0;
    height: 0;
  }

.header__menu { 
  position: absolute;
  right: 0;
  top: 60px;
  width: 250px;
  height: 150px;
  background: #4c4d49;
  border-radius: 3px;
  z-index: 1000;
}

.header__menuItems {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 100%;
}