.app {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
}

h1 {
  font-size: 36px;
  font-weight: 600;
  font-family: 'Montserrat';
}

@media (max-width: 768px) {

h1 {
    font-size: 32px;
}
  }

h2 {
  font-size: 28px;
  font-weight: 600;
  font-family: 'Montserrat';
}

@media (max-width: 768px) {

h2 {
    font-size: 24px;
}
  }

h3 {
  font-size: 24px;
  font-weight: 600;
  font-family: 'Montserrat';
}

@media (max-width: 768px) {

h3 {
    font-size: 20px;
}
  }

h4 {
  font-size: 20px;
  font-family: 'Montserrat';
}

@media (max-width: 768px) {

h4 {
    font-size: 18px;
}
  }

p {
  font-size: 16px;
  font-family: 'Montserrat';
}

@media (max-width: 768px) {

p {
    font-size: 14px;
}
  }