.userLocations {
  position: absolute;
  max-height: 90%;
  min-height: 45%;
  min-width: 280px;
  z-index: 450;
  top: 0;
  background: #7e8287;
  border-radius: 3px;
}

.userLocations--hidden {
  display: none; 
}

.userLocations__container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  position: relative;
  min-height: 360px;
}

.userLocations__inner {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start; 
  padding: 20px;

}

.userLocations__showAll {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  margin-bottom: 20px;
}

.userLocations__closeButton {
  position: absolute;
  top: 0;
  right: -30px;
  width: 30px;
  height: 60px;
  border-style: solid;
  border-width: 0 0 0 10px;
  border-color: transparent transparent transparent #7e8287;
  background: #4c4d49
}

.userLocations__closeButton button {
    width: 30px;
    height: 60px;
    padding: 10px;
    cursor: pointer;
    background: none;
    border: none
  }

.userLocations__closeButton button:before {
      content: '';
      display: block;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 5px 10px 5px 0;
      border-color: transparent #ffffff transparent transparent;
    }

.userLocations__closeButton button span {
      display: none;
    }