.leaflet-popup-content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.marker__address, 
.leaflet-popup-content p {
  font-size: 12px;
  margin: 0;
}

.marker__button {
  font-size: 12px;
  margin: 10px 0; 
}

.marker__title {
  padding: 0;
  background: transparent;
  border: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  font-family: 'Montserrat';
  font-weight: 600;
  color: #d7263d
}

.marker__title:before { 
    display: none;
  }
