.landingPage {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}

.ladingPage__container {
  grid-column: 5/9;
  margin-top: 40px  
}

@media (max-width: 1024px) {

.ladingPage__container {
    grid-column: 4/10  
}
  }

@media (max-width: 768px) {

.ladingPage__container {
    grid-column: 2/12  
}
  }

.landingPage__logo {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.landingPage__changeForm {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}