.login {
  margin: 25px 0 50px;
  padding: 60px 40px;
  border: 1px solid #4c4d49;
  border-radius: 3px;
  background-color: white;
}

.login__title {
  font-size: 36px;
  font-weight: 600;
  font-family: 'Montserrat';
}

@media (max-width: 768px) {

.login__title {
    font-size: 32px;
}
  }

.login__title {
  margin-top: 0;
  text-align: center;
  color: #4c4d49;
}

.login__form {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.login__inputTitle {
  font-size: 16px;
  font-family: 'Montserrat';
}

@media (max-width: 768px) {

.login__inputTitle {
    font-size: 14px;
}
  }

.login__inputTitle {
  color: #4c4d49;
  font-weight: 500;
  margin: 5px 0;
}

.login__input {
  border-radius: 3px;
  height: 15px;
  padding: 10px 5px;
}

.login__button {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  margin-top: 20px;
}
