.location {
  position: relative;
  margin: 10px 0
}

.location:first-child {
    margin-top: 0;
  }

.location:last-child {
    margin-bottom: 0;
  }

.location__name {
  background: none;
  border: none;
  padding-left: 30px;
  cursor: pointer
}

.location__name > span {
    font-size: 16px;
    font-family: 'Montserrat';
  }

@media (max-width: 768px) {

.location__name > span {
    font-size: 14px;
  }
  }

.location__name:hover span{
    opacity: 70%;
  }

.location__deleteButton {
  position: absolute; 
  top: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 20px;
  width: 20px
}

.location__deleteButton > button {
    position: relative;
    cursor: pointer;
    height: 20px;
    width: 20px;
    padding: 0;
    background: none;
    border: none
  }

.location__deleteButton > button:before {
      position: absolute;
      top: 0;
      content: '';
      display: block;
      height: 20px;
      width: 20px;
      background: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='20px' height='20px' viewBox='0 0 512 512' stroke='%23FFFFFF'%3E %3Ctitle%3Eionicons-v5-l%3C/title%3E %3Cline x1='368' y1='368' x2='144' y2='144' style='fill:none;stroke-linecap:round;stroke-linejoin:round;stroke-width:32px'/%3E %3Cline x1='368' y1='144' x2='144' y2='368' style='fill:none;stroke-linecap:round;stroke-linejoin:round;stroke-width:32px'/%3E %3C/svg%3E")
    }

.location__deleteButton > button span {
      display: none;
      visibility: hidden;
      width: 0;
      height: 0;
    }
