.sortLocations {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-top: 20px;
  -ms-flex-pack: distribute;
      justify-content: space-around
}


.sortLocations > button {
    position: relative;
    cursor: pointer;
    height: 30px;
    width: 30px;
    padding: 0;
    background: none;
    border: none;
  }


.sortLocations > button span {
    display: none;
    visibility: hidden;
    width: 0;
    height: 0;
  }


.sortLocations > button:before {
    content: '';
    display: block;
    height: 30px;
    width: 30px;
    position: absolute;
    top: 0;
  }


